body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.search-input {
	font-size: 16px;
	border: none;
	width: 100%;
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}
.search-input:focus {
	border: none;
	outline: none;
	box-shadow: none;
}
.search-input:-ms-clear {
	width: 0px;
	height: 0px;
}

.search-form-wrapper {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	width: 100%;
}

.search-form-icon {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.search-form-clear {
	width: 36px;
	padding: 0px 6px 0px 6px;
}

.search-form-submit {
	width: 48px;
	height: 42px;
	background-color: #e65100;
	cursor: pointer;
}

.clear-icon {
	cursor: pointer;
}

.fadein-animate {
	-webkit-animation-name: search-form-fadein;
	        animation-name: search-form-fadein;
	-webkit-animation-duration: 0.5s;
	        animation-duration: 0.5s;
}

@-webkit-keyframes search-form-fadein {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

@keyframes search-form-fadein {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

.location-form-wrapper {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	max-width: 550px;
	width: 50%;
}

@media screen and (max-width: 768px) {
	.location-form-wrapper {
		width: 75%;
	}
}

@media screen and (max-width: 400px) {
	.location-form-wrapper {
		width: 90%;
	}
}

.location-bar-wrapper {
	width: 100%;
}

.location-detect-location {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	height: 42px;
	width: 48px;
	margin-top: 17px;
	margin-right: 5px;
	border: 1px #757578 solid;
	border-radius: 10px;
	cursor: pointer;
	background-color: white;
}

.location-detect-location:focus {
	outline: none;
}

.location-detect-location:active {
	background-color: #9e9e9e;
}

.current-card-set {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	max-width: 550px;
	-webkit-animation: easein 0.5s;
	        animation: easein 0.5s;
	margin: 20px 0px 20px 0px;
}

.current-card-wrapper {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: flex-start;
	        align-items: flex-start;
	border: #e0e0e0 1px solid;
	border-radius: 10px;
	width: 100%;
	height: 175px;
	overflow: hidden;
}

.current-card-temp {
	font-size: 48pt;
	font-weight: 600;
}

.current-card-summary {
	padding: 2px 0px 2px 0px;
	width: 100%;
	font-size: 16pt;
	background-color: #e65100;
	color: white;
}

.current-card-info {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	margin-top: auto;
	margin-bottom: auto;
	padding-left: 20px;
	padding-right: 20px;
}

.current-card-banner-component {
	margin-right: 10px;
	margin-left: 10px;
	margin-top: 5px;
	margin-bottom: 5px;
}

.current-card-banner-body {
	font-size: 10pt;
	color: #708090;
}

.current-card-banner-subtitle {
	font-weight: 600;
}

@media screen and (min-width: 900px) {
	.current-card-wrapper {
		height: 175px;
	}
}
@media screen and (max-width: 768px) {
	.current-card-wrapper {
		height: 150px;
	}
	.current-card-set {
		width: 75%;
	}
	.current-card-temp {
		font-size: 36pt;
	}
	.current-card-weather {
		font-size: 12pt;
	}
	.current-card-banner-body {
		font-size: 9pt;
	}
	.current-card-banner-component {
		margin-right: 5px;
		margin-left: 5px;
	}
	.current-card-summary {
		font-size: 12pt;
	}
}
@media screen and (max-width: 400px) {
	.current-card-wrapper {
		height: 100px;
	}
	.current-card-set {
		width: 90%;
	}
	.current-card-temp {
		font-size: 24pt;
	}
	.current-card-weather {
		font-size: 12pt;
	}
}

@-webkit-keyframes easein {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

@keyframes easein {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

.alert-warning {
	font-size: 12pt;
	text-align: center;
	color: #ff1744;
	text-decoration: none;
	margin-left: 5px;
	height: 100%;
}

.hourly-forecast-wrapper {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-animation: easein 0.5s;
	        animation: easein 0.5s;
	width: 100%;
	margin-top: 20px;
}

.hourly-forecast-summary {
	font-size: 18pt;
	font-weight: 500;
	width: 90%;
	max-width: 700px;
}

.hourly-forecast-bar {
	margin-top: 15px;
	width: 90%;
	max-width: 700px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	border-radius: 10px;
	overflow: hidden;
}

.hourly-forecast-tickmarks {
	margin-bottom: 10px;
	width: 90%;
	height: 10px;
	max-width: 700px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.hourly-forcast-timestamps {
	width: 90%;
	max-width: 700px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.hourly-timestamp-time {
	font-weight: 600;
	font-size: 12pt;
}

.hourly-timestamp-temp {
	font-size: 12pt;
}

@media screen and (max-width: 768px) {
	.hourly-forecast-summary {
		font-size: 16pt;
	}
	.hourly-timestamp-time {
		font-size: 11pt;
	}
	.hourly-timestamp-temp {
		font-size: 11pt;
	}
}

@media screen and (max-width: 550px) {
	.hourly-timestamp-time {
		font-size: 8pt;
	}
	.hourly-timestamp-temp {
		font-size: 8pt;
	}
}

@media screen and (max-width: 400px) {
	.hourly-forecast-summary {
		font-size: 14pt;
	}
	.hourly-timestamp-time {
		font-size: 6pt;
	}
	.hourly-timestamp-temp {
		font-size: 6pt;
	}
}

@-webkit-keyframes easein {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

@keyframes easein {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

.app {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	width: 100%;
}

